  box-sizing: border-box;
  border-collapse: collapse;
  user-select: none;
}
@font-face {
  font-family: 'roboto';
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff'),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff2');
}

@font-face {
  font-family: 'inter';
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('woff'),
    url(${___CSS_LOADER_URL_REPLACEMENT_3___}) format('woff2');
}

:root {
  font-family: 'roboto', 'inter', system-ui;
}
html,
body {
  margin: 0;
  padding: 0;
  border: none;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(255, 255, 255);
}

.main-container {
  display: flex;
  padding: 50px;
  background-color: rgb(0, 255, 255);
  border-radius: 20px;
}

.menu-container {
  position: relative;
  z-index: 999;
  background-color: rgb(69, 95, 115);
  border-radius: 15px;
}
.menu-container:hover {
  border-bottom-right-radius: 0;
}
.menu-icon {
  width: 40px;
  height: 40px;
  z-index: 999;
}

/* button container */
.dropdown-container {
  display: grid;
  position: absolute;
  top: 40px;
  left: -280px;
  grid-template-rows: repeat(auto-fill, max-content);
  background-color: rgb(69, 95, 115);
  color: white;
  font-weight: 600;
  padding: 40px 10px;
  border-radius: 2px;
}

.button-div {
  display: flex;
  width: 300px;
  gap: 20px;
  justify-content: flex-start;
  padding: 10px;
  border-radius: 50px;
}

.button-div:hover {
  background-color: rgb(18, 32, 42);
}

.button-div:active {
  background-color: skyblue;
}
.icons {
  width: 1.1rem;
  height: 1.1rem;
}
